.body
{
    display: flex;
    flex-wrap: wrap;
    position: relative;
    float: right;
    right: 0px;
    width: calc(100% - 291px);
    text-overflow: ellipsis;
    column-gap: 1cqi;
    row-gap: 20px;
}
.empty
{
    color: white;
    position: relative;
    left: 50%;
    transform: translate(-50% , 0);
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 52px);
    font-size: 30px;
}
.videoDiv
{
    width: 31%;
    padding: 5px;
    border-radius: 12px;
    text-decoration: none;
    aspect-ratio: 1.3;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border:1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    cursor: pointer;
    height: min-content;
}
.videoDiv > div
{
    display: flex;
    flex-direction: column;
}
.thumbnail
{
    width: 100%;
    aspect-ratio: 16 / 9;
    background-size: cover;
    border-radius: 12px 12px 0px 0px;
}
.creator , .title , .views
{
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin: 0.7cqi;
    font-size: 1.2cqi;
}
.title
{
    margin-top: 0cqi;
    margin-bottom: 0cqi;
    font-size: 1.5cqi;
}
.views
{
    font-size: 0.89cqi;
}
@media only screen and (width<=1290px)
{
    .videoDiv{width: 47%;}
    .title{font-size: 2cqi;}
    .creator{font-size: 1.5cqi;}
    .views{font-size: 1.2cqi;}
}
@media only screen and (width<=1040px)
{
    .body
    {
        position: fixed;
        justify-content: space-evenly;
        width: 100%;
        column-gap: 0.3cqi;
        row-gap: 1cqi;
    }
    .videoDiv{width: 30.5%;}
    .creator , .title , .views
    {
        margin-top: 4px;
        margin-bottom: 4px;
    }
}
@media only screen and (width<=678px)
{
    .videoDiv{width: 45%;}
    .body
    {
        column-gap: 0.6cqi;
        row-gap: 2cqi;
    }
    .title{font-size: 3.2cqi;}
    .creator{font-size: 2.5cqi;}
    .views{font-size: 2cqi;}
    .creator , .title , .views
    {
        margin-top: 0.5cqi;
        margin-bottom: 0.6cqi;
    }
    .empty{font-size: 4.5cqi;}
}
@media only screen and (width<=387px)
{
    .videoDiv{width: 90%;}
    .title{font-size: 6cqi;}
    .creator{font-size: 4.5cqi;}
    .views{font-size: 4cqi;}
    .creator , .title , .views
    {
        margin-top: 1.8cqi;
        margin-bottom: 1.9cqi;
    }
}